$accent: #4a93fb;
$background: #2b2d42;
$main: #edf2f4;

.hero {
  // border: 1px solid red;
  background: $background;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 28px;
  min-height: 900px;
  padding-top: 0px;
  overflow: hidden;
  // z-index: -2;
  position: relative;

  &.darken {
    opacity: 0.55;
  }

  .heroTitle {
    color: $main;
    // border: 1px solid red;
    position: relative;
    padding-top: 140px;
    padding-bottom: 30px;
    // display: flex;
    // flex-direction: column;
    // align-items: center;
    h1 {
      font-size: 2.5em;
      text-align: center;
      position: relative;

      &::before {
        content: "Developer";
        position: absolute;
        opacity: 0.08;
        top: -30px;
        transform: translate(-50%);
        left: 50%;
        font-size: 1.3em;
        text-transform: lowercase;
      }
    }
  }

  .heroText {
    // border: 1px solid red;
    width: 80%;
    color: $main;
    text-align: center;
    font-size: 1.2em;
    line-height: 35px;
    padding-bottom: 30px;
    z-index: 1;
    .description {
      padding-bottom: 20px;
    }
    .tagline {
      opacity: 0.7;
    }
  }

  .heroButtons {
    display: flex;
    justify-content: space-between;
    z-index: 1;
  }

  .scrollAni {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 10;

    .circle {
      position: absolute;
      top: 5px; // bounce between 5px and 32 px
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;
      animation: scrollAni ease-in-out 2s;
      //   animation-fill-mode: forwards;
      animation-iteration-count: infinite;
      animation-play-state: play;
    }
  }

  .triangleRotate {
    animation: rotate1 ease-in-out 5s infinite;
  }

  .triangle {
    z-index: 0;
  }

  .triangle1 {
    transform: scale(0.8);
    display: block;
    // transform: rotate(95deg);
    position: absolute;
    bottom: 20%;
    left: 0%;
  }

  .triangle2 {
    // border: 1px solid red;
    transform: scale(0.9);
    position: absolute;
    bottom: 15%;
    right: 15%;
  }

  .triangle3 {
    // border: 1px solid red;
    transform: scale(0.9);
    position: absolute;
    bottom: 40%;
    right: 15%;
  }

  .triangle4 {
    // border: 1px solid red;
    transform: scale(0.9);
    position: absolute;
    bottom: 60%;
    left: 5%;
  }

  .triangle5 {
    // border: 1px solid red;
    transform: scale(0.9);
    position: absolute;
    bottom: 60%;
    left: 5%;
    display: none;
  }
}

@keyframes scrollAni {
  0% {
    top: 5px;
  }
  50% {
    top: 32px;
  }
  100% {
    top: 5px;
  }
}

@media screen and (min-width: 500px) {
  .hero {
    .heroTitle {
      h1 {
        font-size: 2.6em;

        &::before {
          top: -35px;
        }
      }
    }
  }
}

@media screen and (min-width: 600px) {
  .hero {
    .heroTitle {
      padding-top: 160px;
      h1 {
        font-size: 3.4em;
        &::before {
          top: -45px;
        }
      }
    }
    .heroText {
      .description {
        font-size: 1.3em;
      }
    }

    .triangle1 {
      transform: scale(1.1);
      left: 10%;
    }

    .triangle2 {
      transform: scale(1.1);
    }

    .triangle3 {
      bottom: 50%;
      right: 10%;
    }
  }
}

@media screen and (min-width: 900px) {
  .hero {
    .heroTitle {
      padding-top: 170px;
      h1 {
        font-size: 3.8em;
        &::before {
          top: -50px;
        }
      }
    }
    .heroText {
      max-width: 700px;
    }

    .triangle1 {
      transform: scale(1.1);
      left: 10%;
    }

    .triangle2 {
      transform: scale(1.1);
    }
  }
}

@media screen and (min-width: 1000px) {
  .hero {
    .heroTitle {
      padding-top: 170px;
      h1 {
        font-size: 4em;
        &::before {
          top: -55px;
        }
      }
    }

    .triangle1 {
      transform: scale(1.5);
      left: 15%;
    }

    .triangle2 {
      transform: scale(1.3);
      right: 22%;
    }

    .triangle4 {
      left: 10%;
    }
  }
}

@media screen and (min-width: 1200px) {
  .hero {
    .heroTitle {
      padding-top: 170px;
      h1 {
        font-size: 4.5em;
        &::before {
          top: -60px;
        }
      }
      .heroText {
        padding-bottom: 50px;

        .description {
          font-size: 1.5em;
        }
      }
    }

    .triangle1 {
      transform: scale(1.6);
      left: 15%;
    }

    .triangle2 {
      transform: scale(1.4);
      right: 22%;
    }
  }
}

@media screen and (min-width: 1350px) {
  .hero {
    .heroTitle {
      padding-top: 170px;
      h1 {
        font-size: 4.5em;
        &::before {
          top: -60px;
        }
      }
      .heroText {
        .description {
          font-size: 1.5em;
        }
      }
    }

    .triangle1 {
      transform: scale(1.9);
      left: 17%;
    }

    .triangle2 {
      transform: scale(1.7);
      right: 20%;
      bottom: 20%;
    }

    .triangle3 {
      right: 18%;
      bottom: 55%;
    }

    .triangle4 {
      left: 18%;
    }
  }
}

@media screen and (max-width: 400px) {
  .heroButtons {
    flex-direction: column;
    row-gap: 20px;
    align-items: center;
    width: 100%;
    // border: 1px solid red;

    a {
      width: 100%;
      // border: 1px solid red;
    }
  }
}

.typewriter {
  overflow: hidden;
  border-right: 0.15em solid orange;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.15em;
  animation: typing 3.5s steps (40, end), blink-caret 0.75s step-end infinite;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
}

@keyframes rotate-bounce {
  from {
    transform: rotate(30deg);
  }
  to {
    transform: rotate(-30deg);
  }
}

@keyframes rotate1 {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
